.demopage-preview {
  padding: 0 0 0;
}

.demopage-preview > div {
  padding-bottom: 70px;
}

.demopage-preview_item {
  position: relative;
  display: block;
  width: 350px;
  height: 302px;
  margin: 0 auto;
  padding: 17px 18px 86px 16px;
  background: url("../img/mac.png") no-repeat;
  background-size: contain;
  z-index: 10;
}

.demopage-preview_inner {
  position: relative;
  display: block;
  width: 100%;
  height: 100%;
  overflow: hidden;
}

.demopage-preview_inner:after {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  content: '';
  transition: all 0.1s;
}

.demopage-preview_inner:hover:after {
  box-shadow: inset 0 0 11px 2px white;
}

.demopage-preview_inner img {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  transition: all 4s ease 0s;
}

.demopage-preview h3 {
  font-size: 16px;
  line-height: 100%;
  text-align: center;
  text-transform: uppercase;
  padding-top: 13px;
  font-weight: 600;
}

.demopage-img {
  background-position: 0 0;
  background-size: cover;
  transition: all 5.5s;
}

.demopage-img:hover {
  background-position: 0 100%;
}

@media (max-width: 1199px) {
  .demopage-preview {
    padding: 81px 40px 0;
  }
  .demopage-preview_item {
    width: 270px;
    height: 230px;
    padding: 13px 14px 64px 13px;
  }
  .demopage-title {
    margin-bottom: 0;
  }
  ul.demopage-help_list {
    padding: 0;
  }
  ul.demopage-help_list li {
    width: 100%;
    border-left: 1px solid #eee;
  }
  ul.demopage-help_list li a {
    width: 100%;
    text-align: center;
  }
  ul.demopage-help_list li:first-child {
    border-bottom: 1px solid #eee;
  }
}

@media (max-width: 991px) {
  .demopage-preview {
    padding: 81px 0 0;
  }
}

[data-uk-scrollspy] > div {
  opacity: 0;
}
